import React, { Component } from 'react';
import './Tracks.css';
import 'bootstrap/dist/css/bootstrap.min.css';


class Tracks extends Component {
    constructor(props){
        super(props);
        this.state = {
            trackName: props.trackName,
            trackPlayCount: props.trackPlayCount,
            trackArtist: props.trackArtist,
            trackRanking: props.trackRanking
        }
    }

    render() {
    return (
        <div className = "row" className = "tracks-div">
                <p className = "col-xl-1 col-lg-1 col-md-1" id = "ranking">{this.state.trackRanking}</p>
                <div className = "col-xl-11 col-lg-11 col-md-11">
                    <h2 id = "track-name">{this.state.trackName}</h2>
                    <p>Track playcount: {this.state.trackPlayCount}</p>
                </div>
        </div>
    );
  }
}

export default Tracks;
